#referral-main
  //display: flex
  //justify-content: center
  //flex-direction: column
  //align-items: center
  .referral-info-data
    margin-bottom: 24px
    .title
      font-size: 26px
      font-weight: bold
      color: var(--whiteColor)
      margin-bottom: 32px
    .info-part
      display: flex
      justify-content: space-between
      align-items: flex-end
      @media (max-width: 740px)
        display: block
      .info-row
        display: flex
        align-items: flex-start
        justify-content: flex-start
        .info-data
          text-align: left
          margin-right: 32px
          .info-title
            font-size: 14px
            margin-bottom: 4px
          .info-value
            font-size: 16px
            color: var(--whiteColor)
            font-weight: bold
      .claim-button
        @media (max-width: 740px)
          margin-top: 32px
        button
          padding: 8px 24px
          border-radius: 4px
          cursor: pointer
          font-weight: bold
          border: 1px solid var(--highlightFontColor)
          background-color: rgba(0, 0, 0, 0.1)
          color: var(--highlightFontColor)
          &:disabled
            border: 2px solid var(--borderColor)
            cursor: not-allowed
            color: var(--primaryFontColor)


  .referral-link-data
    margin-bottom: 24px
    .title
      font-size: 26px
      font-weight: bold
      color: var(--whiteColor)
      margin-bottom: 8px
    .explain
      margin-bottom: 32px
      b
        color: var(--highlightFontColor)
        font-weight: bold
    .info-part
      display: flex
      align-items: center
      @media (max-width: 740px)
        display: block
      .input-field
        flex: 1
        @media (max-width: 740px)
          width: 100%
      .link-btns
        display: flex
        align-items: center
        justify-content: flex-end
        @media (max-width: 740px)
          justify-content: flex-start
          margin-top: 24px
        .link-btn
          margin: 0 8px
          button
            height: 42px
            padding: 0 18px
            @media (max-width: 740px)
              width: 100%

  .top-referrals-data
    margin-bottom: 24px
    .title
      font-size: 26px
      font-weight: bold
      color: var(--whiteColor)
      margin-bottom: 8px
    .explain
      margin-bottom: 32px
