.token-selector-modal
  width: 220px
  @media (max-width: 720px)
    width: 100vw
  .option-items
    padding-bottom: 12px
    .option-item
      display: flex
      align-items: center
      justify-content: center
      padding: 8px
      cursor: pointer
      &.active
        background: #FFFFFF10
        color: var(--whiteColor)
      &:hover
        background: #FFFFFF05
