.box-wrapper {
  border-radius: 16px;
  /* max-width: 800px; */
  margin-left: auto;
  margin-right: auto;
  background: var(--secondaryBgColor);
  border: 1px solid var(--borderColor);
  padding: 24px;
}

.box-wrapper-no-border {
  border-radius: 4px;
  /* max-width: 800px; */
  margin-left: auto;
  margin-right: auto;
  background: var(--primaryBgColor);
  padding: 16px;
}

@media (max-width: 740px) {
  .box-wrapper {
    padding: 16px;
  }
  .box-wrapper-no-border {
    padding: 8px;
  }
}

.label {
  font-size: 12px;
  margin-bottom: 8px;
  font-weight: bold;
}

/* ============ Input Fields ============ */

.input-field {
  padding: 4px 16px;
  font-size: 14px;
  color: var(--whiteColor);
  height: 44px;
  border-radius: 4px;
  background: var(--primaryBgColor);
  border: none;
  display: flex;
  align-items: center;
}

.input-field:focus {
  outline: none;
}

